* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.logo {
    width: 24px;
    height: 24px;
    vertical-align: sub;
}

body {
    /*font-family: Arial, sans-serif;*/
    line-height: 1.6;
    background-color: #f4f4f4;
    color: #333;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

h1, h2 {
    margin-bottom: 10px;
}

.h1, .h2 {
    margin-top: 16px;
}

.form-group {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 10px;
    margin-bottom: 20px;
    background: none !important;
}

.form-group > div {
    background: #fff;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

.form-group input,
.form-group select,
.form-group textarea {
    color: #888;
    width: 100%;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 10px;
    background: none !important;
}

.form-group input .dark-mode,
.form-group select .dark-mode,
.form-group textarea .dark-mode {
    color: white;
}

.chart-container {
    margin-top: 20px;
}

.bar-chart {
    max-height: 400px; /* Set max height for chart */
}

.form-group {
    vertical-align: top;
    display: inline-block;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    /* gap: 10px; */
    margin-top: 0px;
    margin-bottom: 0px;
    flex-direction: column;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: center;
    background: none !important;

    .checkbox {
        background: none !important;
        display: flex;               /* Enable flexbox */
        flex-direction: column-reverse; /* Reverse the order of children */
        align-items: flex-start;     /* Align items to the start */
        margin: 2px;
        border: 1px solid #8883 !important;
    }

    button {
        background: none !important;
        background-color: #333;
        color: white;
        border: none;
        padding: 0.5rem 1rem;
        border-radius: 0px !important;
        cursor: pointer;
        transition: background-color 0.3s ease;
        visibility: hidden;
    }

    opacity: 0;
    animation: fadeInBlur 0.5s ease-out forwards; /* Added animation */
}

form div {
    margin-bottom: 0rem;
}

fieldset {
    background: none !important;
    display: contents;
    /* margin-inline-start: 2px; */
    /* margin-inline-end: 2px; */
    /* padding-block-start: 0.25em; */
    /* padding-inline-start: 0.25em; */
    /* padding-inline-end: 0.25em; */
    /* padding-block-end: 0.25em; */
    /* min-inline-size: min-content; */
